    goToCheckPoint = () => {
        // Get the nearest checkpoint
        if (this.checkpoint < this.stepFunctions.length - 1) {
            for (let i = 0; i < this.checkpoints.length; ++i) {
                if (this.checkpoint < this.checkpoints[i]) {
                    if (i - 1 <= 0)
                        this.step = -1;
                    else
                        this.step = this.checkpoints[i - 1] - 1;
                    break;
                }
            }
        }

        this.checkpoint = -1;
        this.clearPlayArea();
        if(this.sdk.getIfLevelIsCompleted(this.levelNum) == 1){
            console.log("go to checkpoint")
            this.menuBar.enableSkip(true)
        }
        this.goToNextStep();
    }

    checkForCheckPointAndContinue = () => {
        this.clearPlayArea();
        if (localStorage.getItem('stepOnHintclicked') && this.sdk.getIfLevelIsCompleted(this.levelNum) == 0) {
            this.showHintCompleteDialog();
        }
        else
        {
            this.goToNextStep();
        }        
    }

    level25Completed = () => {
        let progress = 0
        if (localStorage.getItem("level2progress")) {
            progress = parseFloat(localStorage.getItem("level2progress"));
        }

        if (progress < 0.25) {
            progress = 0.25;
            localStorage.setItem("level2progress", progress);
        }
        this.menuBar.level2ProgressBar.setProgress(progress);

        this.timer.addTimeOut(0.5, "nextstep");
    }

