    level100Completed = () => {
        let progress = 0
        if (localStorage.getItem("level4progress")) {
            progress = parseFloat(localStorage.getItem("level4progress"));
        }

        progress = 1.0;
        localStorage.setItem("level4progress", progress);
        this.menuBar.level4ProgressBar.setProgress(progress);

        this.timer.addTimeOut(0.5, "nextstep");
    }

    clearPlayArea = () => {
        this.mascot.disable();
        if (this.menuBar) this.menuBar.enableReplayHand(false);
        this.removeAllShapes();

    }

    clearPlayAreaAndNext = () => {
        this.clearPlayArea();

        this.goToNextStep();
    }

