    goToCheckPoint = () => {
        // Get the nearest checkpoint
        if (this.checkpoint < this.stepFunctions.length - 1) {
            for (let i = 0; i < this.checkpoints.length; ++i) {
                if (this.checkpoint < this.checkpoints[i]) {
                    if (i - 1 <= 0)
                        this.step = -1;
                    else
                        this.step = this.checkpoints[i - 1] - 1;
                    break;
                }
            }
        }
        
        this.checkpoint = -1;
        this.clearPlayArea();
        this.goToNextStep();
    }

    checkForCheckPointAndContinue = () => {
        this.clearPlayArea();
        if (localStorage.getItem('stepOnHintclicked') && this.sdk.getIfLevelIsCompleted(this.levelNum) == 0) {
            this.showHintCompleteDialog();
        }
        else
        {
            this.goToNextStep();
        }        
    }
    jumptoValidation = () =>{
        this.step = 103;
        this.goToNextStep();
    }

    level25Completed = () => {
        let progress = 0
        if (localStorage.getItem("level1progress")) {
            progress = parseFloat(localStorage.getItem("level1progress"));
        }

        if (progress < 0.25) {
            progress = 0.25;
            localStorage.setItem("level1progress", progress);
        }
        this.menuBar.level1ProgressBar.setProgress(progress);

        this.timer.addTimeOut(0.5, "nextstep");
    }

