    validateShapeSelection = (shapeName, shapeObject) => {
        this.time = 0;
        console.log("this.waitingFor: " + this.waitingFor);
        if(this.highlightWrongShape)    this.scene.remove(this.highlightWrongShape.texture);
        if(this.pulsatingArrow) this.scene.remove(this.pulsatingArrow.texture);
        
        if(this.waitingFor == shapeName || this.waitingForCard == shapeName){
            this.highlightCorrectShape = this.getTexture2(this.assetManager.textures[10], "highlightCorrectShape", true, 0, shapeObject.position.x, 0, shapeObject.position.y, -1);
            this.scene.add(this.highlightCorrectShape.texture);
            this.playAudioPath('./audio/common/For_correct_answer.mp3');
            this.results.push(1);
            this.sdk.setCorrectAttempts(this.levelNum);
            this.waitingForTap = false;
            this.waitingFor = "";
            this.waitingForCard = "";
            this.timer.addTimeOut(1, "nextstep");
        }else{
            setTimeout(() => {
                if(this.pulsatingArrow) this.scene.remove(this.pulsatingArrow.texture);
            }, 2000);
            if(this.waitingFor && this.waitingForCard){
                this.resultsCopy = this.results.slice(-3);;
                
                const allEqual = arr => arr.every(val => val === 0);
                const elements = allEqual(this.resultsCopy);
                if(this.resultsCopy.length == 3 && elements)
                {
                    this.clueArrowAfter15Seconds();
                    
                }
            }
            this.highlightWrongShape = this.getTexture2(this.assetManager.textures[13], "highlightWrongShape", true, 0, shapeObject.position.x, 0, shapeObject.position.y, -1);
            this.scene.add(this.highlightWrongShape.texture);
            this.playAudioPath('./audio/common/For_the_wrong_answer.mp3');
            this.results.push(0);
            this.sdk.setWrongAttempts(this.levelNum);
            this.playAudioPath('./audio/level1/ISD_A_L1_6_merge.mp3');
        }
    }
    clueArrowAfter15Seconds = () =>{
        switch (this.waitingForCard) {
            case 'objectCard1': 
                this.pulsatingArrow = this.getTexture2(this.assetManager.textures[62], "pulsatingArrow", true, this.objectCard1.xref, this.objectCard1.xdisp+70, this.objectCard1.yref, this.objectCard1.ydisp+40, -1);
                this.scene.add(this.pulsatingArrow.texture);
                break;
            case 'objectCard2': 
                this.pulsatingArrow = this.getTexture2(this.assetManager.textures[62], "pulsatingArrow", true, this.objectCard2.xref, this.objectCard2.xdisp+70, this.objectCard2.yref, this.objectCard2.ydisp+40, -1);
                this.scene.add(this.pulsatingArrow.texture);
                break;
            case 'objectCard3': 
                this.pulsatingArrow = this.getTexture2(this.assetManager.textures[62], "pulsatingArrow", true, this.objectCard3.xref, this.objectCard3.xdisp+70, this.objectCard3.yref, this.objectCard3.ydisp+40, -1);
                this.scene.add(this.pulsatingArrow.texture);
                break;
            case 'objectCard4':
                this.pulsatingArrow = this.getTexture2(this.assetManager.textures[62], "pulsatingArrow", true, this.objectCard4.xref, this.objectCard4.xdisp+70, this.objectCard4.yref, this.objectCard4.ydisp+40, -1);
                this.scene.add(this.pulsatingArrow.texture);
                break;
        }
        setTimeout(() => {
            if(this.pulsatingArrow) this.scene.remove(this.pulsatingArrow.texture);
        }, 2000);
    }
    
    
    voiceOutSelection = () => {
        if(this.waitingFor){
            this.currentAudioPath = './audio/level1/ISD_A_L1_6.mp3';
            this.playAudioPath(this.currentAudioPath);
        }
    }

