    enableTrialCriteria = () => {
        this.considerPoints = false;
        this.results = [];
        this.goToNextStep();
    }

    enableCompletionCriteria = () => {
        console.log("enableCompletionCriteria");
        this.considerPoints = true;
        this.results = [];
        this.goToNextStep();
    }

    validateForCompletion = () => {
        console.log("validateForCompletion: this.result: " + this.results);

        const allEqual = arr => arr.every(val => val === 1);
        const elements = allEqual(this.results);

        if (!elements) {
            this.clearPlayArea();
            this.step -= 15;
            
            this.checkpoint = this.step;
            this.goToCheckPoint();
        }
        else {
            this.goToNextStep();
        }
    }

    goToGameplay = () =>{

        this.checkpoint = 10;
        this.goToCheckPoint();
    }

    checkIfPresentationPlayedAndSkip = () =>{

        if(this.sdk.getPresentationCompleteForLevel(this.levelNum) == 1 && !localStorage.getItem("skipClickedAfterPrevious") && !this.skipContinue){
            if(this.sdk.getIfLevelIsCompleted(this.levelNum) == 1){
                this.menuBar.enableSkip(true)
            }
            this.goToGameplay();
        }else if(localStorage.getItem("skipClickedAfterPrevious")){
            this.checkpoint = localStorage.getItem("skipClickedAfterPrevious");
            localStorage.removeItem('skipClickedAfterPrevious');
            if(this.sdk.getIfLevelIsCompleted(this.levelNum) == 1){
                this.menuBar.enableSkip(true)
            }
            this.goToCheckPoint();
        }else{
            this.timer.addTimeOut(0.5, "nextstep");
        }

    }
