import * as THREE from 'three'
import AssetManager from './AssetManager'
import Texture from './Texture'
import Config from './Config'
import Utils from './Utils'
import MenuBar from './MenuBar'
import Timer from './Timer'
import Mascot from './Mascot'
import { DragControls } from 'three/examples/jsm/controls/DragControls'
import Level0Dialog1 from './Level0Dialog1'
import MenuDialog from './MenuDialog'
import Level from './Level'
import HomeActionDialog from './HomeActionDialog'
import Logs from './Logs'

class Level0 extends Level {
    constructor(loadingManager, scene, camera, renderer,sound,  backgroundSound,listner, sdk, callback) {
        var imagePaths = [
            './textures/level_0/BGRepeatable.png',
            './textures/common/interactions/Hand_Cursor3.png',
            './textures/level_0/Object_card_outline.png',

            './textures/level_0/Book_1.png',
            './textures/level_0/Ball_2.png',
            './textures/level_0/set1/Icecream_1.png',
            './textures/level_0/set1/Pineapple_1.png',
            './textures/level_0/set2/Frog_1.png',
            './textures/level_0/set2/Goose_1.png',
            './textures/level_0/set3/Gloves_1.png',
            './textures/level_0/set3/Hammer_1.png',
            './textures/level_0/set4/Icecream_5.png',
            './textures/level_0/set4/Jar_1.png',
            './textures/level_0/set5/Butterfly_1.png',
            './textures/level_0/set5/Cabbage_1.png',
        ];

        console.log("imagePaths: "+imagePaths);
        super(loadingManager, scene, camera, renderer,sound, backgroundSound,listner,  imagePaths, false, sdk);

        this.callback = callback;
        this.positionalObjs = [];
        this.levelNum = 0;
        this.playingAgain = false; 
        this.dragControls = null;
        this.listener =  listner;
        this.checkpoint = -1;
        this.step = -1;
        this.Logs = new Logs(this, sdk);

        this.setupSteps();
    }

    dispose() {
        if (this.menuDialog) this.menuDialog.dispose();
        if (this.endDialog) this.endDialog.dispose();
        if (this.homeActionDialog) this.homeActionDialog.dispose();

        document.removeEventListener('mousedown', this.onDocumentMouseDown, false);
        document.removeEventListener('mousemove', this.onDocumentMouseMove, false);
        document.removeEventListener('touchdown', this.onDocumentMouseDown, false);

        this.disposeSuper();
    }

    updateForResize = () => {
        this.updateForResizeSuper();

        for (let i = 0; i < this.positionalObjs.length; ++i) {
            this.positionalObjs[i].updateForResize();
        }
    }

    createScene = () => {
        // Background
        let backgroundImage = this.getTexture2(this.assetManager.textures[0], "background", true, 0, 0, 0, 0, -100, new THREE.Vector2(3, 1), THREE.MirroredRepeatWrapping);
        this.scene.add(backgroundImage.texture);

        this.updateForResize();

        document.addEventListener('mousedown', this.onDocumentMouseDown, false);
        document.addEventListener('mousemove', this.onDocumentMouseMove, false);
        document.addEventListener('touchdown', this.onDocumentMouseDown, false);

        this.goToNextStep();

        this.startBackgroundMusic();
    }

    update = () => {
        const delta = this.clock.getDelta();
        this.updateSuper(delta);
        if (this.endDialog)
            this.endDialog.update();

    }

    onDocumentMouseDown = (event) => {
        if (!this.enableEvents)
            return;

        this.mouse.x = (event.clientX / this.renderer.domElement.clientWidth) * 2 - 1;
        this.mouse.y = - (event.clientY / this.renderer.domElement.clientHeight) * 2 + 1;

        this.raycaster.setFromCamera(this.mouse, this.camera);

        var intersects = this.raycaster.intersectObjects(this.scene.children);

        if (intersects.length > 0) {
            console.log(intersects[0].object.name);
        }
    }

    onDocumentMouseMove = (event) => {
        if (!this.enableEvents)
            return;

        this.mouse.x = (event.clientX / this.renderer.domElement.clientWidth) * 2 - 1;
        this.mouse.y = - (event.clientY / this.renderer.domElement.clientHeight) * 2 + 1;
    }

    onMenuBarLoaded = () => {
        this.menuBar.enableHome(true);
        this.menuBar.enableBack(false);
        this.menuBar.enableHint(false);
        this.menuBar.enableReward(true);
        this.menuBar.enableAudioPlayButton(true);
        this.menuBar.enableSkip(this.sdk.getPresentationCompleteForLevel(this.levelNum) == 1);
        
        this.onMenuBarLoadedSuper();
    }

    onHintAction = () => {
        this.stopAll();
        this.checkpoint = this.step;

        this.step = -1;
        this.goToNextStep();
    }

    onHomeAction = () => {
        if (this.menuDialog || this.endDialog)
            return;

        this.setEnableEvents(false);

        this.stopAll();
        this.checkpoint = this.step;

        this.homeActionDialog = new HomeActionDialog(this.loadingManager, this.scene, this.camera, this.renderer, this.sound,this.listener, this);
    }

    onAudioReplayAction = () => {
    }

    stopAll = () => {
        this.clearPlayArea();
        if (this.hand) this.scene.remove(this.hand.texture);
        this.stopAllAudio();
        this.timer.clearAll();
    }

    setupSteps = () => {
        this.checkpoints = [0, 7, 10, 13, 16, 19, 22];

        this.stepFunctions = [
            this.showStep0,                 // 0
            this.showStep1,                 // 1
            this.showStep2,                 // 2
            this.showStep3,                 // 3
            this.showStep4,                 // 4
            this.showStep5,                 
            this.removeShapedObjectsAndNext,
            this.iceAndPine1,
            this.iceAndPine2,
            this.removeShapedObjectsAndNext,
            this.frogAndGoose1,
            this.frogAndGoose2,
            this.removeShapedObjectsAndNext,
            this.glovesAndHammer1,
            this.glovesAndHammer2,
            this.removeShapedObjectsAndNext,
            this.icecreamAndJar1,
            this.icecreamAndJar2,
            this.removeShapedObjectsAndNext,
            this.butterflyAndCabbage1,
            this.butterflyAndCabbage2,
            this.removeShapedObjectsAndNext,
            this.showEnd,               
        ];
    }

    goToCheckPoint = () => {
        // Get the nearest checkpoint
        if (this.checkpoint < this.stepFunctions.length - 1) {
            for (let i = 0; i < this.checkpoints.length; ++i) {
                if (this.checkpoint < this.checkpoints[i]) {
                    if (i - 1 <= 0)
                        this.step = -1;
                    else
                        this.step = this.checkpoints[i - 1] - 1;
                    break;
                }
            }
        }

        this.checkpoint = -1;
        this.clearPlayArea();

        this.goToNextStep();
    }

    clearPlayArea = () => {
        this.removeShapedObjects();
        if(this.mascot )this.mascot.disable();
    }

    clearPlayAreaAndNext = () => {
        this.clearPlayArea();

        this.goToNextStep();
    }

    showStep0 = () => {
        if(!this.playingAgain){
            this.Logs.logEventLevelStartDuration(this.levelNum);
        }
        this.sdk.setLevelStarted(this.levelNum);
        this.timer.addTimeOut(1.0, "nextstep");
        
    }

    showStep1 = () => {
        this.mascot.enable(2);
        this.playAudioPath('./audio/level0/ISD_A_L0_1.mp3');
        this.mascot.enableTalkingAnimation(5);
        this.timer.addTimeOut(6, "nextstep");
    }

    cardObjects = () =>{
        this.objectCard1 = this.getTexture2(this.assetManager.textures[2], "objectCard1", true, 0, -200, 0, 0, -50);
        this.scene.add(this.objectCard1.texture);

        this.objectCard2 = this.getTexture2(this.assetManager.textures[2], "objectCard2", true, 0, 200, 0, 0, -50);
        this.scene.add(this.objectCard2.texture);
    }

    showStep2 = () => {

        this.mascot.enable(0, 1);

        this.playAudioPath('./audio/level0/ISD_A_L0_2.mp3');
        this.mascot.enableTalkingAnimation(5);
        this.timer.addTimeOut(6, "nextstep");
    }

    showStep3 = async () => {
        this.mascot.disable();
        this.cardObjects();
        this.ball1 = this.getTexture2(this.assetManager.textures[4], "ball1", true, this.objectCard1.xref, this.objectCard1.xdisp, this.objectCard1.yref, this.objectCard1.ydisp, -1);
        this.scene.add(this.ball1.texture);

        this.ball2 = this.getTexture2(this.assetManager.textures[4], "ball2", true, this.objectCard2.xref, this.objectCard2.xdisp, this.objectCard2.yref, this.objectCard2.ydisp, -1);
        this.scene.add(this.ball2.texture);
        this.playAudioPath('./audio/level0/ISD_A_L0_3.mp3');
        this.timer.addTimeOut(4, "nextstep");
    }

    showStep4 = async () => {
        this.scene.remove(this.ball2.texture);

        this.book = this.getTexture2(this.assetManager.textures[3], "book", true, this.objectCard2.xref, this.objectCard2.xdisp, this.objectCard2.yref, this.objectCard2.ydisp, -1);
        this.scene.add(this.book.texture);
        this.playAudioPath('./audio/level0/ISD_A_L0_4.mp3');
        this.timer.addTimeOut(4, "nextstep");
    }

    showStep5 = async () => {
        this.playAudioPath('./audio/level0/ISD_A_L0_5.mp3');
        this.timer.addTimeOut(5, "nextstep");
    }


    removeShapedObjects = () =>{
        const thisObjects = this;
        const allShapes = [this.book,this.ball1,this.ball2,this.icecream1,this.icecream2,this.pineApple, this.goose1, this.goose2,this.frog, this.gloves1,this.gloves2,this.hammer,
                            this.icecream3,this.icecream4,this.jar, this.butterfly1,this.butterfly2,this.cabbage, this.objectCard1,this.objectCard2];
        let scene = this.scene;

        allShapes.forEach(function (item) {
            if (item && item.texture) {
                scene.remove(item.texture);
                thisObjects.removeAsset(item.texture);
            }
        });
    }

    removeShapedObjectsAndNext = () =>{
        this.removeShapedObjects();
        this.timer.addTimeOut(0.5, "nextstep");
    }

    iceAndPine1 = async() =>{
        this.cardObjects();
        this.icecream1 = this.getTexture2(this.assetManager.textures[5], "icecream1", true, this.objectCard1.xref, this.objectCard1.xdisp, this.objectCard1.yref, this.objectCard1.ydisp, -1);
        this.scene.add(this.icecream1.texture);

        this.icecream2 = this.getTexture2(this.assetManager.textures[5], "icecream2", true, this.objectCard2.xref, this.objectCard2.xdisp, this.objectCard2.yref, this.objectCard2.ydisp, -1);
        this.scene.add(this.icecream2.texture);
        this.playAudioPath('./audio/level0/ISD_A_L0_3.mp3');
        this.timer.addTimeOut(4, "nextstep");
    }

    iceAndPine2 = async () => {
        this.scene.remove(this.icecream2.texture);

        this.pineApple = this.getTexture2(this.assetManager.textures[6], "pineApple", true, this.objectCard2.xref, this.objectCard2.xdisp, this.objectCard2.yref, this.objectCard2.ydisp, -1);
        this.scene.add(this.pineApple.texture);
        this.playAudioPath('./audio/level0/ISD_A_L0_4.mp3');
        this.timer.addTimeOut(4, "nextstep");
    }
    
    frogAndGoose1 = async() =>{
        this.cardObjects();
        this.goose1 = this.getTexture2(this.assetManager.textures[8], "goose1", true, this.objectCard1.xref, this.objectCard1.xdisp, this.objectCard1.yref, this.objectCard1.ydisp, -1);
        this.scene.add(this.goose1.texture);

        this.goose2 = this.getTexture2(this.assetManager.textures[8], "goose2", true, this.objectCard2.xref, this.objectCard2.xdisp, this.objectCard2.yref, this.objectCard2.ydisp, -1);
        this.scene.add(this.goose2.texture);
        this.playAudioPath('./audio/level0/ISD_A_L0_3.mp3');
        this.timer.addTimeOut(4, "nextstep");
    }

    frogAndGoose2 = async () => {
        this.scene.remove(this.goose2.texture);

        this.frog = this.getTexture2(this.assetManager.textures[7], "frog", true, this.objectCard2.xref, this.objectCard2.xdisp, this.objectCard2.yref, this.objectCard2.ydisp, -1);
        this.scene.add(this.frog.texture);
        this.playAudioPath('./audio/level0/ISD_A_L0_4.mp3');
        this.timer.addTimeOut(4, "nextstep");
    }

    glovesAndHammer1 = async() =>{
        this.cardObjects();
        this.gloves1 = this.getTexture2(this.assetManager.textures[9], "gloves1", true, this.objectCard1.xref, this.objectCard1.xdisp, this.objectCard1.yref, this.objectCard1.ydisp, -1);
        this.scene.add(this.gloves1.texture);

        this.gloves2 = this.getTexture2(this.assetManager.textures[9], "gloves2", true, this.objectCard2.xref, this.objectCard2.xdisp, this.objectCard2.yref, this.objectCard2.ydisp, -1);
        this.scene.add(this.gloves2.texture);
        this.playAudioPath('./audio/level0/ISD_A_L0_3.mp3');
        this.timer.addTimeOut(4, "nextstep");
    }

    glovesAndHammer2 = async () => {
        this.scene.remove(this.gloves2.texture);

        this.hammer = this.getTexture2(this.assetManager.textures[10], "hammer", true, this.objectCard2.xref, this.objectCard2.xdisp, this.objectCard2.yref, this.objectCard2.ydisp, -1);
        this.scene.add(this.hammer.texture);
        this.playAudioPath('./audio/level0/ISD_A_L0_4.mp3');
        this.timer.addTimeOut(4, "nextstep");
    }

    icecreamAndJar1 = async() =>{
        this.cardObjects();
        this.icecream3 = this.getTexture2(this.assetManager.textures[11], "icecream3", true, this.objectCard1.xref, this.objectCard1.xdisp, this.objectCard1.yref, this.objectCard1.ydisp, -1);
        this.scene.add(this.icecream3.texture);

        this.icecream4 = this.getTexture2(this.assetManager.textures[11], "icecream4", true, this.objectCard2.xref, this.objectCard2.xdisp, this.objectCard2.yref, this.objectCard2.ydisp, -1);
        this.scene.add(this.icecream4.texture);
        this.playAudioPath('./audio/level0/ISD_A_L0_3.mp3');
        this.timer.addTimeOut(4, "nextstep");
    }

    icecreamAndJar2 = async () => {
        this.scene.remove(this.icecream4.texture);

        this.jar = this.getTexture2(this.assetManager.textures[12], "jar", true, this.objectCard2.xref, this.objectCard2.xdisp, this.objectCard2.yref, this.objectCard2.ydisp, -1);
        this.scene.add(this.jar.texture);
        this.playAudioPath('./audio/level0/ISD_A_L0_4.mp3');
        this.timer.addTimeOut(4, "nextstep");
    }

    butterflyAndCabbage1 = async() =>{
        this.cardObjects();
        this.butterfly1 = this.getTexture2(this.assetManager.textures[13], "butterfly1", true, this.objectCard1.xref, this.objectCard1.xdisp, this.objectCard1.yref, this.objectCard1.ydisp, -1);
        this.scene.add(this.butterfly1.texture);

        this.butterfly2 = this.getTexture2(this.assetManager.textures[13], "butterfly2", true, this.objectCard2.xref, this.objectCard2.xdisp, this.objectCard2.yref, this.objectCard2.ydisp, -1);
        this.scene.add(this.butterfly2.texture);
        this.playAudioPath('./audio/level0/ISD_A_L0_3.mp3');
        this.timer.addTimeOut(4, "nextstep");
    }

    butterflyAndCabbage2 = async () => {
        this.scene.remove(this.butterfly2.texture);

        this.cabbage = this.getTexture2(this.assetManager.textures[14], "cabbage", true, this.objectCard2.xref, this.objectCard2.xdisp, this.objectCard2.yref, this.objectCard2.ydisp, -1);
        this.scene.add(this.cabbage.texture);
        this.playAudioPath('./audio/level0/ISD_A_L0_4.mp3');
        this.timer.addTimeOut(4, "nextstep");
    }

    showEnd = () => {
        this.Logs.logEventLevelEndDuration(this.levelNum);

        this.menuBar.updateRewardText(this.sdk.getTotalRewardPoints());
        this.endDialog = new Level0Dialog1(this.loadingManager, this.scene, this.camera, this.renderer, this.sound,this.listener, this);
        this.setEnableEvents(false);

        this.levelCompletionMusic = new Audio('./audio/common/For_level_completion.mp3');
        this.levelCompletionMusic.play();
        this.playAudioPath('./audio/common/GL_A_5.mp3');
    }

    playAgain = () => {
        if (this.endDialog)
            this.endDialog.dispose();
        this.endDialog = null;
        this.step = -1;
        this.playingAgain = true;
        
        this.goToNextStep();
        this.menuBar.enableSkip(this.sdk.getPresentationCompleteForLevel(this.levelNum) == 1);
        this.setEnableEvents(true);
    }

    playNextLevel = () => {
        this.callback.playNextGame();
        this.setEnableEvents(true);
    }

    playNewGame = () => {
        if (this.menuDialog)
            this.menuDialog.dispose();
        this.menuDialog = null;
        this.step = -1;
        this.goToNextStep();

        this.setEnableEvents(true);
    }
}

export default Level0