    dispose() {
        if (this.endDialog) this.endDialog.dispose();
        if (this.homeActionDialog) this.homeActionDialog.dispose();

        document.removeEventListener('mousedown', this.onDocumentMouseDown, false);
        document.removeEventListener('mousemove', this.onDocumentMouseMove, false);
        document.removeEventListener('touchdown', this.onDocumentMouseDown, false);

        this.disposeSuper();
    }

    updateForResize = () => {
        this.updateForResizeSuper();

        for (let i = 0; i < this.positionalObjs.length; ++i) {
            this.positionalObjs[i].updateForResize();
        }
    }

    createScene = () => {
        // Background

        let backgroundImage = this.getTexture2(this.assetManager.textures[3], "background", true, 0, 0, 0, 0, -100, new THREE.Vector2(3, 1), THREE.RepeatWrapping);
        this.scene.add(backgroundImage.texture);

        let bottomLand = this.getTexture2(this.assetManager.textures[135], "bottomland", true, 0, 0, -1, 18, -97, new THREE.Vector2(3, 1));
        this.scene.add(bottomLand.texture);
        this.positionalObjs.push(bottomLand);

        let floatingCloudsBG = this.getTexture2(this.assetManager.textures[136], "floatingCloudsBG", true, 0, 0, 0, 0, -99, new THREE.Vector2(3, 1));
        this.scene.add(floatingCloudsBG.texture);
        this.positionalObjs.push(floatingCloudsBG);

        this.updateForResize();

        document.addEventListener('mousedown', this.onDocumentMouseDown, false);
        document.addEventListener('mousemove', this.onDocumentMouseMove, false);

        document.addEventListener('touchdown', this.onDocumentMouseDown, false);

        this.goToNextStep();

        this.startBackgroundMusic();
    }

    update = () => {
        const delta = this.clock.getDelta();
        this.time += delta;

        this.updateSuper(delta);

        if (this.endDialog)
            this.endDialog.update();

        if(this.menuBar && this.step > 10){
            this.menuBar.enableBack(true);
            this.menuBar.enableHint(true);
        }
        if (this.waitingForTap && (this.waitingFor1 || this.waitingFor2) && this.time >= this.waitSelectionDuration) {
            this.time = 0;
            this.voiceOutSelection();
            this.clueArrowAfter15Seconds();
        }

    }

    onDocumentMouseDown = (event) => {
        if(!this.enableEvents)
            return;
    
        this.mouse.x = (event.clientX / this.renderer.domElement.clientWidth) * 2 - 1;
        this.mouse.y = - (event.clientY / this.renderer.domElement.clientHeight) * 2 + 1;

        this.raycaster.setFromCamera(this.mouse, this.camera);

        var intersects = this.raycaster.intersectObjects(this.scene.children);

        if (intersects.length > 0) {
            let intersectName = intersects[0].object.name;
            let intersect = intersects[0].object
            console.log(intersectName);

            var names = ['objectParrotSlothCard1','objectParrotSlothCard2','sloth_1','parrot_1','branchSP_1','branchSP_2','fenceSP_1','fenceSP_2','girlMotherSP_1','girlMotherSP_2','treeSP_1','treeSP_2','stonesSP_1','stonesSP_2','boardSP_1','boardSP_2',
                        'objectLionGiraffeCard1','objectLionGiraffeCard2','lion_1','giraffe_1','fenceLG_1','fenceLG_2','girlLG_1','girlLG_2','pondLG_1','pondLG_2','treeLG_1','treeLG_2',
                        'objectLFishandFrogCard1','objectLFishandFrogCard2','fishFF_1','frogFF_1','leafpondFF_1','leafpondFF_2','girlMotherFF_1','girlMotherFF_2','pondFF_1','pondFF_2','treeFF_1','treeFF_2',
                        'objectBoyandGirlCard1','objectBoyandGirlCard2','boyBG_1','girlBG_1','schoolBG_1','schoolBG_2','objectBusCarCard1','objectBusCarCard2','busBC_1','CarBC_1','policeBC_1','policeBC_2','whistleBC_1','whistleBC_2',
                        'objectCowSheepCard1','objectCowSheepCard2','cowCS_1','sheepCS_1', 'objectCarBusCard1', 'objectCarBusCard2','carCB_1','busCB_1',
                        'objectBenchShopCard1','objectBenchShopCard2','benchBS_1','shopBS_1','girlBS_1','girlBS_2','objectBenchBushCard1','objectBenchBushCard2','benchBB_1','bushBB_1','girlBB_1','girlBB_2','dogBB_1','dogBB_2',
                        'objectDogCatCard1','objectDogCatCard2','catDC_1','dogDC_1','bushDC_1','bushDC_2','girlDC_1','girlDC_2', 'objectFlower2Flower1Card1','objectFlower2Flower1Card2','flower1FF_1','flower2FF_1','bushFF_1','bushFF_2',
                        'objectBed1Bed2Card1','objectBed1Bed2Card2','bed1BB_1','bed2BB_1','boyBB_1','boyBB_2', 'objectPhoneBooksCard1','objectPhoneBooksCard2','phonePB_1','booksPB_1','tablePB_1','tablePB_2','chairPB_1','chairPB_2',
                        'objectLaptopBooksCard1','objectLaptopBooksCard2','laptopLB_1','booksLB_1','potLB_1','potLB_2','tableLB_1','tableLB_2', 'objectBoy1Boy2Card1','objectBoy1Boy2Card2','boy1BB_1','boy2BB_1','homeBB_1','homeBB_2',
                        'objectFish1Fish2Card1','objectFish1Fish2Card2','fish1FF_1','fish2FF_1','boy1FF_1','boy1FF_2', 'objectWhaleSharkCard1','objectWhaleSharkCard2','whaleWS_1','sharkWS_1','woman1WS_1','woman1WS_2',
                        'objectFruitShellCard1','objectFruitShellCard2','fruitFS_1','shellFS_1','objectCard1','objectCard2','elephant_1','ostrich_1',,'fence_1','fence_2','girlMother_1','girlMother_2','tree_1','tree_2','bush_1','bush_2'];
            if (names.indexOf(intersectName) !== -1) {
                if (this.waitingForTap) {
                    this.validateShapeSelection(intersectName,intersect);
                }
            }

        }
    }

    onDocumentMouseMove = (event) => {
        this.mouse.x = (event.clientX / this.renderer.domElement.clientWidth) * 2 - 1;
        this.mouse.y = - (event.clientY / this.renderer.domElement.clientHeight) * 2 + 1;
    }

    onMenuBarLoaded = () => {
        this.menuBar.enableHome(true);
        this.menuBar.enableBack(false);
        this.menuBar.enableHint(false);
        this.menuBar.enableReward(true);
        this.menuBar.enableAudioPlayButton(true);
        this.menuBar.enableSkip(this.sdk.getIfLevelIsCompleted(this.levelNum) == 1);

        this.onMenuBarLoadedSuper();
    }

    onHintAction = () => {
        this.stopAll();
        localStorage.setItem("stepOnHintclicked",this.step);
        this.checkpoint = this.step;
        this.menuBar.enableBack(false);
        this.menuBar.enableHint(false);
        this.menuBar.enableSkip(true);
        this.step = 0;
        this.goToNextStep();
    }

    onHomeAction = () => {
        if(this.menuDialog || this.endDialog)
            return;

        this.stopAll();
        this.setEnableEvents(false);
        this.checkpoint = this.step;
    
        this.homeActionDialog = new HomeActionDialog(this.loadingManager, this.scene, this.camera, this.renderer, this.sound,this.listener, this);
    }

    onAudioReplayAction = () => {
        if(this.isAnyAudioPlaying()){
            return;
        }
        
        if (this.currentAudioPath && this.step > 10)
            this.playAudioPath(this.currentAudioPath);
    }

    stopAll = () => {
        this.mascot.disable();
        this.removeAllShapes();
        if (this.tick) this.scene.remove(this.tick.texture);
        if (this.hand) this.scene.remove(this.hand.texture);
        this.stopAllAudio();
        this.waitingForTap = false;
        this.timer.clearAll();
    }

