    validateShapeSelection = (shapeName, shapeObject) => {
        this.time = 0;
        console.log("this.waitingFor: " + this.waitingFor1, this.waitingFor2);
        if(this.highlightShape1)    this.scene.remove(this.highlightShape1.texture);
        if(this.highlightShape2)    this.scene.remove(this.highlightShape2.texture);
        if(this.pulsatingArrow) this.scene.remove(this.pulsatingArrow.texture);
        
        if(this.waitingFor1 == shapeName || this.waitingFor2 == shapeName){
            switch (this.waitingForCombo) {
                case "slothandparrotCombo":
                    this.highlighting2shapes(19,23,this.sloth_1,this.parrot_1);
                    break;
                case "lionandgiraffeCombo":
                    this.highlighting2shapes(32,33,this.giraffe_1,this.lion_1);
                    break;
                case "fishandfrogCombo":
                    this.highlighting2shapes(41,42,this.fishFF_1,this.frogFF_1);
                    break;
                case "boyandgirlCombo":
                    this.highlighting2shapes(47,48,this.boyBG_1,this.girlBG_1);
                    break;
                case "busandcarCombo":
                    this.highlighting2shapes(55,54,this.busBC_1,this.CarBC_1);
                    break;
                case "sheepandcowCombo":
                    this.highlighting2shapes(59,60,this.sheepCS_1,this.cowCS_1);
                    break;
                case "carandbusCombo":
                    this.highlighting2shapes(64,65,this.carCB_1,this.busCB_1);
                    break;
                case "benchandshopCombo":
                    this.highlighting2shapes(70,71,this.shopBS_1,this.benchBS_1);
                    break;
                case "bushandbenchCombo":
                    this.highlighting2shapes(77,78,this.bushBB_1,this.benchBB_1);
                    break;
                case "dogandcatCombo":
                    this.highlighting2shapes(84,85,this.dogDC_1,this.catDC_1);
                    break;
                case "flower2andflower1Combo":
                    this.highlighting2shapes(91,90,this.flower2FF_1,this.flower1FF_1);
                    break;
                case "bed1andbed2Combo":
                    this.highlighting2shapes(96,97,this.bed1BB_1,this.bed2BB_1);
                    break;
                case "phoneandbooksCombo":
                    this.highlighting2shapes(103,104,this.phonePB_1,this.booksPB_1);
                    break;
                case "laptopandbooksCombo":
                    this.highlighting2shapes(110,111,this.laptopLB_1,this.booksLB_1);
                    break;
                case "boy1andboy2Combo":
                    this.highlighting2shapes(116,117,this.boy1BB_1,this.boy2BB_1);
                    break;
                case "fish1andfish2Combo":
                    this.highlighting2shapes(122,123,this.fish1FF_1,this.fish2FF_1);
                    break;
                case "whaleandsharkCombo":
                    this.highlighting2shapes(129,128,this.sharkWS_1,this.whaleWS_1);
                    break;
                case "fruitandshellCombo":
                    this.highlighting2shapes(133,134,this.shellFS_1,this.fruitFS_1);
                    break;
                case "elephantandostrichCombo":
                    this.highlighting2shapes(11,12,this.elephant_1,this.ostrich_1);
                    break;
            }
            
            this.playAudioPath('./audio/common/For_correct_answer.mp3');
            this.results.push(1);
            this.sdk.setCorrectAttempts(this.levelNum);
            this.waitingForTap = false;
            this.waitingFor1 = "";
            this.waitingFor2 = "";
            this.waitingForCombo = "";
            this.timer.addTimeOut(2, "nextstep");
        }else{
            this.clueAudioPlayed = false;
            setTimeout(() => {
                if(this.pulsatingArrow) this.scene.remove(this.pulsatingArrow.texture);
            }, 2000);
            if(this.waitingForCombo && (this.waitingFor1 || this.waitingFor2) ){
                this.resultsCopy = this.results.slice(-5);
                
                const allEqual = arr => arr.every(val => val === 0);
                const elements = allEqual(this.resultsCopy);
                if(this.resultsCopy.length == 5 && elements)
                {
                    this.clueAudioPlayed = true;
                    this.clueArrowAfter15Seconds();
                    this.playAudioPath('./audio/level1/ISD_A_L1_6_merge.mp3');
                }
            }
            
            this.playAudioPath('./audio/common/For_the_wrong_answer.mp3');
            this.results.push(0);
            this.sdk.setWrongAttempts(this.levelNum);
            if(!this.clueAudioPlayed){
                this.playAudioPath('./audio/level1/ISD_A_L1_6_merge.mp3');
                this.clueAudioPlayed = false;
            }
        }
    }

